import { Component, OnInit } from '@angular/core';
import moment from 'moment';
import { DataService } from '../services/data.service';
import { BehaviorSubject, merge, Observable } from 'rxjs';
import { instanceOfInvoiceLog, InvoiceLog } from '../models/invoice-log.model';
import { LoadingStates } from '../models/loading-states.enum';
import { take } from "rxjs/operators";
import { HttpResponse } from "@angular/common/http";

@Component({
  selector: 'app-generator',
  templateUrl: './generator.component.html'
})
export class GeneratorComponent implements OnInit {
  currentMonth: string;
  isSending = false;
  isLoading = true;
  invoiceLog$: BehaviorSubject<InvoiceLog> = new BehaviorSubject<InvoiceLog>(null);
  invoiceProcessingStep: number;
  invoiceProcessingText: string;
  LoadingStates = LoadingStates;

  constructor(private dataService: DataService) {}

  ngOnInit(): void {
    moment.locale('pl');
    this.currentMonth = moment().format('MMMM YYYY');
    this.invoiceExistsForCurrentMonth();
  }

  invoiceExistsForCurrentMonth(): void {
    const month = moment(this.currentMonth, 'MMMM YYYY').format('MMYYYY');
    this.dataService.getInvoiceLogForCurrentMonth(month)
      .subscribe((currentMonthInvoiceLog) => {
        if (!!currentMonthInvoiceLog) {
          this.invoiceLog$.next(currentMonthInvoiceLog);
          this.invoiceProcessingStep = 5;
        }
        this.isLoading = false;
      });
  }

  downloadInvoicePdf(): void {
    this.dataService.getInvoicePdf().pipe(take(1)).subscribe((data: HttpResponse<Blob>) => {
      const header = data.headers.get('content-disposition');
      const fileName = header.split('=').pop();
      const newBlob = new Blob([data.body], { type: "application/pdf" });

      const downloadURL = URL.createObjectURL(newBlob);
      const link = document.createElement('a');
      link.href = downloadURL;
      link.download = decodeURI(fileName);
      link.click();
    });
  }

  sendInvoiceEmail(): void {
    this.isSending = true;
    const streams: Observable<number | InvoiceLog> = merge(this.dataService.processingProgress$, this.dataService.processInvoice());

    streams.subscribe((data: number | InvoiceLog) => {
      if (typeof data === 'number') {
        this.invoiceProcessingStep = data;
        this.invoiceProcessingText = Object.values(this.LoadingStates)[this.invoiceProcessingStep];
      } else if (data && instanceOfInvoiceLog(data)) {
        this.isSending = false;
        this.invoiceLog$.next(data as InvoiceLog);
        this.invoiceProcessingStep = 4;
      }
    });
  }
}


