import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { exhaustMap, take, tap } from 'rxjs/operators';
import { environment } from '../../../environments/environment';
import { InvoiceLog } from '../models/invoice-log.model';
import { InvoiceData } from '../models/invoice-data.model';
import { EmailResponse } from '../models/email-response.model';
import { SmsResponse } from '../models/sms-response.model';

@Injectable()
export class DataService {
  public processingProgress$ = new BehaviorSubject<number>(1);

  constructor(private httpClient: HttpClient) {
  }

  getInvoiceData(): Observable<InvoiceData> {
    return this.httpClient.get<InvoiceData>(environment.url + '/invoice-data');
  }

  getInvoicePdf(): Observable<HttpResponse<unknown>> {
    return this.httpClient.get(environment.url + '/invoice-data/pdf', {
      observe: 'response',
      responseType: 'blob' as 'json'});
  }

  processInvoice(): Observable<InvoiceLog> {
    return this.sendEmail().pipe(
      // tap(emailData => console.log('Data received from email request', emailData)),
      // delay(3000),
      exhaustMap((emailResponse: EmailResponse) => this.sendSms(emailResponse)),
      tap(() => this.processingProgress$.next(2)),
      // delay(3000),
      // tap(smsData => console.log('Data received from sms request', smsData)),
      tap(() => this.processingProgress$.next(3)),
      // delay(3000),
      exhaustMap((smsResponse: SmsResponse) => this.sendInvoiceLog(smsResponse)),
      // tap(invoiceLogData => console.log('Data received from saving invoice log request', invoiceLogData)),
      tap(() => this.processingProgress$.next(4)),
      tap(() => this.processingProgress$.complete()),
      take(1),
    );
  }

  sendEmail(): Observable<EmailResponse> {
    return this.httpClient.post<EmailResponse>(environment.url + '/email', {});
  }

  sendSms(emailResponse: EmailResponse): Observable<SmsResponse> {
    return emailResponse && emailResponse.responseCode === 250 ?
      // of({
      //   responseCode: 'OK',
      //   errorId: 0,
      //   message: 'test',
      //   data: 15112011
      // }) : of(null);
      this.httpClient.post<SmsResponse>(environment.url + '/sms', {}) : of(null);
  }

  sendInvoiceLog(smsResponse: SmsResponse): Observable<InvoiceLog> {
    return smsResponse && smsResponse.responseCode === 'OK' && smsResponse.errorId === 0 ?
      this.httpClient.post<InvoiceLog>(environment.url + '/invoice-log', {}) : of(null);
  }

  getInvoiceLogForCurrentMonth(currentMonth: string): Observable<InvoiceLog> {
    return this.httpClient.get<InvoiceLog>(`${environment.url}/invoice-log/${currentMonth}`).pipe(
      take(1)
    );
  }
}
